<template>
	<div  class="CheckOrder">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>按单复核-首页</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div class="import">
				<span>SO单号</span>
				<input  placeholder="请输入内容"  @focus="g_focus('OrderKey')"  @keyup.enter="check"   ref="OrderKey"  v-model="OrderKey" ></input>
				<i v-if="OrderKey" @click="clearInput('OrderKey')" class="iconfont icon-cuowu"></i>									
			</div>
			<div class="import">
				<span>箱号</span>
				<input  placeholder="请输入内容" @keyup.enter="CheckStart"   ref="CaseNo"  v-model="CaseNo" ></input>
				<i v-if="CaseNo" @click="clearInput('CaseNo')" class="iconfont icon-cuowu"></i>				
			</div>
			<div class="import">
				<span>商品总数</span>
				<input   class="readonly"   ref="SumQty"  v-model="SumQty" readonly></input>			
				</div>
			<div class="import">
				<span>商品品项数</span>
				<input    class="readonly"  ref="CountSku"  v-model="CountSku" readonly></input>				
			</div>
			<div class="import">
				<span>待复核总数</span>
				<input     class="readonly"  ref="SumPreCheckQty"  v-model="SumPreCheckQty" readonly></input>							
			</div>							
			<div class="g_doubleBtn">
				<div @click="CheckStart">开始复核</div>
				<div @click="checkDetail">查看明细</div>
			</div>
		</div>
		<div v-if="tipsHide" @touchmove.prevent class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p  @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"CheckOrder",
		data(){
			return{
				OrderKey:'',
				CaseNo:'',
				SumQty:'',//商品总数
				CountSku:'',//商品品相数
				tipsHide:false,//提示框是否出现
				msg:"",//提示信息
				inputFocus:"",
				StorerKey:'',
				CheckOrder:"",											
				StoreKey_select:"",
				BoxType:'ByOrder',
				SumCheckedQty:'',//已复核总数
				Status:'',//订单状态
			}
		},
		computed:{
			SumPreCheckQty:function(){
				return this.SumQty-this.SumCheckedQty;
			}
		},
		mounted() {
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
			this.$refs['OrderKey'].focus();
			
			if(this.OrderKey){
            	this.check();
            	if(this.CaseNo){
            		this.$refs['CaseNo'].select();
            	}           	
            }
		},
		created(){
			var that = this;
			// SO单号
			if(this.$route.query.OrderKey){

				that.OrderKey = that.$route.query.OrderKey;
			}
			//箱号
			if(this.$route.query.CaseNo){

				that.CaseNo = that.$route.query.CaseNo;
			}			          
		},
		methods:{
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				this[inputName] = qrcode;

				if(inputName == "OrderKey") {

					this.check();
					
				}else if(inputName == "CaseNo") {

					this.CheckStart();
				}
			},
			//返回上一页 首页
			toIndex:function(){
				this.$router.push({
					name:"Index",
				})
			},
			// 获取复核单总数
			check:function(){
				var that = this;				
				if(this.OrderKey =='') {
 				that.msg = "SO单号不能为空";
				that.tipsShow("OrderKey");
				that.inputFocus = "OrderKey";
				return false;
 			  }								
				var currentUrl = this.HOST + "RFCheck.do";

				var postData = {
					OrderKey : that.OrderKey,
					BoxType:that.BoxType,
				}

				this.httpRequest(currentUrl,postData,function(res){

					if(res.data.data.length){
                        that.SumQty=res.data.data[0].SumQty;
                        that.CountSku=res.data.data[0].CountSku;
						that.SumCheckedQty=res.data.data[0].SumCheckedQty;
						that.StorerKey=res.data.data[0].StorerKey;
						that.OrderKey=res.data.data[0].OrderKey;
						that.Status=res.data.data[0].Status;
						that.$refs['CaseNo'].focus();						
					}else{

						that.tipsShow("");

						that.msg = "未查询到数据";

						that.inputFocus = "Orderkey";
					}
				},"OrderKey")
			},
			
			//开始复核--跳转到 按单复核-复核页面
			CheckStart:function(){         
				var that = this;
				if(this.OrderKey =='') {
 				that.msg = "SO单号不能为空";
				that.tipsShow("OrderKey");
				that.inputFocus = "OrderKey";
				return false;
 			    }
				if(this.Status=='02' || this.Status=='04' || this.Status=='09'){				
 				that.msg = "订单未配货,无法复核";
				that.tipsShow("OrderKey");
				that.inputFocus = "OrderKey";
				return false;
				}
				if(this.Status=='98' || this.Status=='99'){
				that.msg = "订单已取消,无需复核";
				that.tipsShow("OrderKey");
				that.inputFocus = "OrderKey";
				return false;
				}
				if(this.SumPreCheckQty<=0){
				that.msg = "订单已经复核完成,无需再次复核!";
				that.tipsShow("OrderKey");
				that.inputFocus = "OrderKey";
				return false;
				}

				var currentUrl = this.HOST + "RFCheckStart.do";

				var postData = {
					OrderKey : that.OrderKey,
					BoxType : that.BoxType,
				    StorerKey: that.StorerKey,
					CaseNo: that.CaseNo,
				}

				this.httpRequest(currentUrl,postData,function(res){										
					that.$router.push({
					    name:"CheckOrder_check",
						query:{
							OrderKey : that.OrderKey,
					        CaseNo: that.CaseNo,
					        StorerKey:that.StorerKey,
						}
					})
				})
			},
			//按单复核-查看明细
			checkDetail:function(){				
				var that = this;				
				if(this.OrderKey =='') {
 				that.msg = "SO单号不能为空";
				that.tipsShow("OrderKey");
				that.inputFocus = "OrderKey";
				return false;
 			 }
				var currentUrl = this.HOST + "RFCheckDetail.do";

				var postData = {
					OrderKey : that.OrderKey,
					 BoxType : that.BoxType,
				}

				this.httpRequest(currentUrl,postData,function(res){
					if(res.data.data.length){
						that.$store.state.CheckOrder_detailData = res.data.data;
						that.$router.push({
						name:"CheckOrder_detail",
						query:{
								OrderKey : that.OrderKey,
							}
						})
					}else{

						that.tipsShow("");

						that.msg = "未查询到数据";

					}
					
				},)
			}
			
		}
	}
</script>
<style scoped lang="less">
.CheckOrder{
	&>.container{
		margin-top: 110/50rem;
		input::placeholder{
			font-size: 25/50rem;
		}
		input{
			font-size: 25/50rem;
		}		
		
		&>div.import{
			width:100%;
			height: 55/50rem;
			margin-bottom: 20/50rem;
			box-sizing: border-box;
			padding: 0 20/50rem;
			position: relative;
			overflow: hidden;
			span{
				width: 150/50rem;
				margin-top: 10/50rem;
				text-align: right;
				float: left;
				margin:0  5/50rem;
			}
			input{
				margin-left: 10/50rem;
				width:510/50rem;	
				height:55/50rem;
				float: left;				
				padding:0 10/50rem;
				box-sizing: border-box;
			}					
			i{
				position:absolute;
				right:70/50rem;
				font-size: 30/50rem;
				top: 13/50rem;
			}	
			&>input.readonly{
				background-color: #F0F0F0;
				outline: none;
			}						    			
		}

	}
}
@media screen and (max-width: 330px){
	.CheckOrder{
		&>.container{
			input::placeholder{
				font-size: 35/50rem;
			}
			input{
				font-size: 35/50rem;
			}
			&>div.import{
				width:100%;
				height: 65/50rem;
				margin-top: 10/50rem;
				span{
					width: 180/50rem;
				}				
				input{
					margin-left: 0;
					height: 65/50rem;
				}			
				i{
					position:absolute;
					right:60/50rem;
					font-size: 42/50rem;
					top: 10/50rem;
				}								
			}
			&>div.top{
				margin-top: 20/50rem;
			}
			&>div.top1{
				margin-top: 10/50rem;
			}
			
			&>div.butto{
				margin-bottom: 0;
			}
		}
	}
}
@media screen and (max-width: 277px){
	.CheckOrder{
		&>.container{
			font-size: 42/50rem;
			input::placeholder{
				font-size: 40/50rem;
			}
			input{
				font-size: 40/50rem;
			}
			&>div.import{
				height: 85/50rem;
				span{
					width: 210/50rem;
					height: 75/50rem;
					line-height: 75/50rem;
					padding-right: 5/50rem;
					padding-left: 5/50rem;
				}
							
				input{					
					width:460/50rem;	
					height:75/50rem;
					margin: 0;
					padding: 0 10/50rem;
				}
				i{
					position:absolute;
					right:70/50rem;
					font-size: 48/50rem;
					top: 13/50rem;
				}
			}
		}
	}
}
</style>


